.dropdown .dropbtn {
  font-size: inherit; 
  text-transform: capitalize; 
  border: none;
  outline: none;
  color: white;
  
  background-color: inherit;
  font-family: inherit;
  margin: 0;
  cursor: pointer;
}

.dropbtn i {
  margin-left: 5px;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #202327;
  padding: 20px;
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.25);
  
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
  
}
.dropdown-content li {
  margin-bottom: 5px;
  margin-left: 0px;
}


