.HomeScreen {
  margin-top: 65px;
  height: 100vh;
  padding-top: 0px;
  padding-left: 20px;
  padding-right: 20px;
}

.Product_Container {
  display: flex;
}

.Products {
  width: 690px;
  color: white;
  padding-top: 0px;
}

/* .Products_Scroll{
    margin-top: 30px;
    display: inline-block;
    width: 1000px;
} */

.Products_Scroll {
  width: 800px;
  margin-top: 30px;
}

.Products_Scroll2 {
  width: 800px;
  margin-top: 30px;
}

.Scroll_container {
  overflow: hidden;
  height: 250px;
  display: flex;
}

