.Link_Card {
  text-decoration: none;
  color: white;
}

.Card {
  height: 205px;
  width: 150px;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 10px;
  padding: 10px 10px;

  margin-right: 40px;
}

.Card_Img img {
  width: 120px;
  height: 100px;

  border-radius: 5px;
}

.Product_Title {
  font-size: 14px;
  font-weight: 500;
  color: white;
  margin-top: 5px;
}

.Product_Brief {
  font-size: 10px;
  color: white;
  letter-spacing: 0.6px;
  margin-top: 2px;
  margin-bottom: 5px;
}

.Explore_btn {
  display: flex;
  width: 80px;
  height: 25px;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.548);
  color: black;
  font-size: 12px;
  font-weight: 500;
  border-radius: 10px;
  margin-left: 20px;
  margin-top: 10px;
}

.Explore_btn:hover {
  background-color: rgba(255, 255, 255, 0.959);
}

.Card2 {
  height: 120px;
  width: 245px;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 10px;
  padding: 10px 10px;
  display: flex;
  margin-right: 40px;
}

.Card_Img2 img {
  width: 90px;
  height: 100px;
  border-radius: 5px;
}

.Product_Details {
  margin-left: 10px;
}

.Product_Title2 {
  font-size: 15px;
  font-weight: 500;
  color: white;
}

.Product_Brief2 {
  font-size: 10px;
  color: white;

  margin-top: 2px;
  letter-spacing: 0.6px;
  margin-bottom: 10px;
}

.Rating {
  margin-left: 5px;

  font-size: 10px;
  font-weight: 500;
}
.Price {
  margin-top: 0px;
  font-size: 16px;
  font-weight: 500;
  margin-left: 0px;
}

.Btn_Add {
  font-size: 12px;
  font-weight: 500;
  border-radius: 10px;
  width: 80px;
  margin-left: 45px;
  margin-top: 5px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.548);
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Btn_Add:hover {
  background-color: rgba(255, 255, 255, 0.959);
}

.Premium_Products {
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  background-color: #202327;
  color: white;
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.25);
  margin-left: 70px;
  width: 300px;
  height: 100vh;
}

.Premium_Card {
  padding: 0px;
  width: 100%;
}

.Premium_CardImg img {
  height: 220px;
  width: 100%;
  border-radius: 5px;
}

.Details {
  margin-top: 40px;
  color: white;
}
.Premium_Price {
  margin-top: 10px;
  font-size: 20px;
  font-weight: 200;

  float: right;
}

.Premium_Title {
  font-size: 22px;
  font-weight: 500;
}

.Premium_Brief {
  font-size: 14px;
  margin-top: 10px;
  font-weight: 200;
}

.Premium_Btn {
  height: 40px;
  background-color: rgba(255, 255, 255, 0.089);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 500;

  margin-top: 30px;
}

.Premium_Btn:hover {
  background-color: rgba(255, 255, 255, 0.219);
}

.Ads {
  padding-top: 20px;
  padding-left: 10px;
  width: 180px;
}
.Ads_Container p {
  margin-top: 40px;
  color: rgba(255, 255, 255, 0.377);
  font-size: 50px;
  font-weight: 700;
}

.Products_Scroll3{
  display: none;
}

@media only screen and (max-width: 414px) {
  .HomeScreen {
    margin-top: 0px;
    height: 100vh;
    padding-top: 60px;
    padding-left: 10px;
    padding-right: 20px;
  }

  .Product_Container {
    display: inline-block;
  }

  .Products {
    width: 100%;
    color: white;
    padding-top: 0px;
  }

  .Products_Scroll {
    width: 414px;
    margin-top: 30px;
    height: 200px;
  }

  .Products_Scroll2 {
    width: 414px;
    margin-top: 85px;
    height: 190px;
  }

  .Products_Scroll3 {
    width: 414px;
    margin-top: 0px;
    height: 180px;
    display: inline-block;
  }

  .Scroll_container {
    overflow: auto;
    height: 250px;
    display: flex;
  }

  .Premium_Products {
    display: none;
  }

  .Ads {
    width: 350px;
  }
  .Ads_Container p {
    margin-top: 0px;
    color: rgba(255, 255, 255, 0.377);
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;
  }

  .Card {
    height: 205px;
    width: 150px;
    background-color: rgba(255, 255, 255, 0.048);
    border-radius: 10px;
    padding: 10px 10px;
  
    margin-right: 20px;
  }
  

  .Card2 {
    height: 120px;
    width: 245px;
    background-color: rgba(255, 255, 255, 0.048);
    border-radius: 10px;
    padding: 10px 10px;
    display: flex;
    margin-right: 20px;
  }
  
}

