.MyOrder_Screen{
    
    height: 100vh;
    display: flex;
    padding: 100px 60px 60px 60px;
  
    color: white;
   
    justify-content: space-around;
}


table, th, td {
    border: 1px solid rgb(61, 61, 61);
    border-collapse: collapse;
  }
  th, td {
    padding: 10px;
  }
  th {
    text-align: left;
    font-weight: 500;
  }
  td {
      text-align: center;
      font-size: 12px;
  }

  caption {
      margin-bottom: 30px;
      font-size: 25px;
      font-weight: 500;
  }


  @media only screen and (max-width: 414px) {
    /* .MyOrder_Screen{
    
      height: 100%;
      width: 100%;
      display: inline-block;
      padding: 100px 0px 0px 0px;
    
      color: white;
     
      justify-content: space-around;
  } */


  .MyOrder_Screen{
    
    height: 100%;
    display: inline-block;
    padding: 100px 25px 0px 25px;
  
    color: white;
   
    justify-content: none;
}


table, th, td {
    border: 1px solid rgb(61, 61, 61);
    border-collapse: collapse;
  }
  th, td {
    padding: 10px;
  }
  th {
    text-align: left;
    font-weight: 500;
  }
  td  {
      text-align: center;
      font-size: 8px;
  }

  td p  {
    
    font-size: 8px;
}

  caption {
      margin-bottom: 30px;
      font-size: 25px;
      font-weight: 500;
  }
  }



  @media only screen and (max-width: 375px) {
    .MyOrder_Screen{
    
      height: 100%;
      display: inline-block;
      padding: 100px 5px 0px 5px;
    
      color: white;
     
      justify-content: none;
  }
  
  
  table, th, td {
      border: 1px solid rgb(61, 61, 61);
      border-collapse: collapse;
    }
    th, td {
      padding: 10px;
    }
    th {
      text-align: left;
      font-weight: 500;
    }
    td  {
        text-align: center;
        font-size: 7px;
    }
  
    td p  {
      
      font-size: 7px;
  }
  
    caption {
        margin-bottom: 30px;
        font-size: 25px;
        font-weight: 500;
    }

  }